import { fetchPACData, fetchPACRegistry } from '../../../api/fetchPACData';
import {
  getLocalStoragePremiumData,
  LocalStoragePremiumKey,
  PACPremiumData,
  setLocalStoragePremiumData,
} from './premium';
import {
  getLocalStorageData,
  LocalStorageKey,
  PACData,
  PACRegistryData,
  setLocalStorageData,
} from './shared';

export async function getPACData({
  shouldRefetch = false,
}: {
  shouldRefetch?: boolean;
}): Promise<PACData | PACPremiumData> {
  let shouldUpdate = shouldRefetch;

  const registry = await getPACRegistryData();

  console.warn('--------------------------------');
  console.warn('The shouldUpdate', shouldUpdate);
  console.warn('--------------------------------');

  const { hasPremium } = await getLocalStoragePremiumData(
    LocalStoragePremiumKey.HasPremium,
  );

  const { isLitePAC } = await getLocalStorageData(LocalStorageKey.IsLitePAC);

  if (hasPremium) {
    const { pacPremiumData } = await getLocalStoragePremiumData(
      LocalStoragePremiumKey.PACPremiumData,
    );

    if (!shouldUpdate && pacPremiumData !== undefined) {
      return pacPremiumData;
    }

    const registryUrl =
      isLitePAC && registry.premiumLite
        ? registry.premiumLite
        : registry.premium;

    console.warn('--------------------------------');
    console.warn('The registryUrl', registryUrl);
    console.warn('--------------------------------');

    const data = await fetchPACData<PACPremiumData>(registryUrl);

    await setLocalStoragePremiumData({ pacPremiumData: data });
    return data;
  }

  const { pacData } = await getLocalStorageData(LocalStorageKey.PACData);

  if (!shouldUpdate && pacData !== undefined) {
    return pacData;
  }

  const registryUrl =
    isLitePAC && registry.freeLite ? registry.freeLite : registry.free;

  console.warn('--------------------------------');
  console.warn('The registryUrl', registryUrl);
  console.warn('--------------------------------');

  const data = await fetchPACData<PACData>(registryUrl);

  await setLocalStorageData({ pacData: data });

  return data;
}

export async function getPACRegistryData(): Promise<PACRegistryData> {
  const data = await fetchPACRegistry();

  return data;
}
