import { BrowserInfo, detect } from 'detect-browser';
import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '../app/storage/local/shared';
import {
  type FailedVideoOpenMessage,
  type Message,
  type ShowVideoLoaderMessage,
} from '../app/messages/shared';
import { Actions } from '../app/actions/shared';
import { getSessionMetaApiData } from '../app/storage/session/metaApiData';
import { initNetworkConnectionMonitor } from './network/monitor';
import { initIcon } from './icon/utils';
import { updatePacScript } from './pac-script/updatePacScript';
import { createSentryScope } from '@app/sentry/createSentryScope';
import { LocalStoragePremiumKey } from '@app/storage/local/premium';
import {
  BASE_URL,
  replaceTimecodeWithSavedTime,
} from './urls/replaceTimecodeWithSavedTime';
import { handleProxyControlledByOtherExtensions } from './network/handleProxyControlledByOtherExtensions';
import { moveUserIdAndDeviceIdFromCookiesToLocalStorage } from './user/moveUserIdFromCookiesToLocalStorage';
import { moveUserIdAndDeviceIdFromLocalStorageToCookiesForOldUsers } from './user/moveUserIdAndDeviceIdFromLocalStorageToCookiesForOldUsers';
import { checkShouldSkipCaptcha } from './redirect/utils';
import { featureStore } from '@app/features/list';
import { getConfigsUrl } from 'src/utils';

export const backgroundScope = createSentryScope('background');

// Storage listeners

chrome.storage.onChanged.addListener(async (changes, namespace) => {
  if (namespace === 'local') {
    if (changes[LocalStoragePremiumKey.HasPremium]) {
      const hasPremium = changes[LocalStoragePremiumKey.HasPremium].newValue;

      backgroundScope.setTag('hasPremium', hasPremium);
      backgroundScope.setExtras({
        hasPremium,
        namespace,
      });

      backgroundScope.captureMessage('Has premium changed', 'info');
    }
  }
});

// Runtime listeners
chrome.runtime.onInstalled.addListener(async (details) => {
  try {
    featureStore.init();

    if (details.reason === chrome.runtime.OnInstalledReason.INSTALL) {
      backgroundScope.setExtra('details', details);
      backgroundScope.captureMessage('Extension installed', 'info');

      try {
        const { supportedBrowsers } = await getSessionMetaApiData();

        const welcomeUrl = await getConfigsUrl(process.env.WELCOME_URL);

        if (welcomeUrl) {
          await chrome.tabs.create({ url: welcomeUrl.toString() });
        }

        const browser = detect();
        const isBrowserSupported =
          browser instanceof BrowserInfo &&
          supportedBrowsers.includes(browser.name);
        if (!isBrowserSupported) {
          await setLocalStorageData({ isBrowserError: true });
          if ('openPopup' in chrome.action) {
            const window = await chrome.windows.getCurrent();
            if (window.id) {
              console.warn('onInstalled RELOADING WINDOW');

              await chrome.windows.update(window.id, { focused: true });
            }
            await chrome.action.openPopup({ windowId: window.id });
          }
        }

        await updatePacScript();
        await moveUserIdAndDeviceIdFromCookiesToLocalStorage();
      } catch (err) {
        console.error(err);
        await setLocalStorageData({ currentState: 'error' });
      }
    }

    if (details.reason === chrome.runtime.OnInstalledReason.UPDATE) {
      backgroundScope.setExtra('details', details);
      backgroundScope.captureMessage('Extension updated', 'info');

      await updatePacScript();

      await moveUserIdAndDeviceIdFromLocalStorageToCookiesForOldUsers();
    }
  } catch (error) {
    console.error(error);
  }
});

chrome.runtime.onMessage.addListener(async (message: Message, { tab }) => {
  if (message.action === Actions.FailedVideoOpen && tab?.id) {
    chrome.tabs.onUpdated.addListener(
      async function listener(tabIdUpdated, changeInfo) {
        if (tabIdUpdated === tab.id && changeInfo.status === 'complete') {
          await chrome.tabs.sendMessage<FailedVideoOpenMessage>(
            tabIdUpdated,
            message,
          );
          chrome.tabs.onUpdated.removeListener(listener);
        }
      },
    );
  }

  if (message.action === Actions.SaveTime && tab?.id) {
    const { time, videoId } = message.payload;

    await chrome.storage.local.set({
      [LocalStorageKey.SavedTime]: {
        [videoId]: Math.round(time),
      },
    });
  }
});

chrome.runtime.onStartup.addListener(async () => {
  backgroundScope.captureMessage('Extension started', 'info');
  await updatePacScript();
});

// Tabs listeners

chrome.tabs.onActivated.addListener(async (activeInfo) => {
  try {
    const { currentState } = await getLocalStorageData([
      LocalStorageKey.CurrentState,
    ]);

    const tab = await chrome.tabs.get(activeInfo.tabId);

    const isConnected = currentState === 'connected';
    const isCaptchaUrl =
      tab.url?.includes('google.com/sorry') &&
      tab.title?.includes('youtube.com/watch');

    const shouldSkipCaptcha = await checkShouldSkipCaptcha();

    if (
      !shouldSkipCaptcha &&
      tab.url &&
      tab.id &&
      isConnected &&
      isCaptchaUrl
    ) {
      const newUrl = await replaceTimecodeWithSavedTime(tab.url);

      await chrome.tabs.update(tab.id, { url: newUrl });
    }
  } catch (error) {
    console.error(error);
  }
});

chrome.tabs.onUpdated.addListener(async (tabId) => {
  try {
    const tabs = await chrome.tabs.query({
      active: true,
      currentWindow: true,
    });

    const activeTab = tabs.length > 0 ? tabs[0] : undefined;

    const { currentState } = await getLocalStorageData([
      LocalStorageKey.CurrentState,
    ]);

    const isActiveTab = activeTab?.id === tabId;

    const isConnected = currentState === 'connected';

    const isCaptchaUrl =
      activeTab?.url?.includes('google.com/sorry') &&
      activeTab?.title?.includes('youtube.com/watch');

    const shouldSkipCaptcha = await checkShouldSkipCaptcha();

    if (
      !shouldSkipCaptcha &&
      activeTab?.id &&
      activeTab?.url &&
      isActiveTab &&
      isConnected &&
      isCaptchaUrl
    ) {
      // Wait for the page to be fully loaded
      await new Promise<void>((resolve) => {
        const checkLoadStatus = async () => {
          const tabStatus = await chrome.tabs.get(activeTab.id!);
          if (tabStatus.status === 'complete') {
            resolve();
          } else {
            setTimeout(checkLoadStatus, 100);
          }
        };
        checkLoadStatus();
      });

      await chrome.tabs.sendMessage<ShowVideoLoaderMessage>(activeTab.id, {
        action: Actions.ShowPageLoader,
      });

      const newUrl = await replaceTimecodeWithSavedTime(activeTab.url);

      console.warn('tab updated started', newUrl);

      await chrome.tabs.update(activeTab.id, { url: newUrl });
    }

    // if the url does not include www we need to add it
    if (
      activeTab?.id === tabId &&
      activeTab.url?.startsWith('https://youtube.com') &&
      isConnected
    ) {
      const newUrl = activeTab.url.replace(
        '//youtube.com',
        '//www.youtube.com',
      );

      const urlWithTimecode = await replaceTimecodeWithSavedTime(newUrl);

      await chrome.tabs.update(tabId, { url: urlWithTimecode });
    }

    if (
      activeTab?.id === tabId &&
      activeTab.url?.startsWith('https://youtu.be') &&
      isConnected
    ) {
      const prevURL = new URL(activeTab.url);
      const params = new URLSearchParams(prevURL.search);
      const newUrl = new URL(BASE_URL); // https://www.youtube.com/watch

      for (const [key, value] of params.entries()) {
        newUrl.searchParams.set(key, value);
      }

      newUrl.searchParams.set('v', activeTab.url.split('/').pop()!);
      newUrl.searchParams.set('feature', 'youtu.be');

      const urlWithTimecode = await replaceTimecodeWithSavedTime(
        newUrl.toString(),
      );

      await chrome.tabs.update(tabId, { url: urlWithTimecode });
    }
  } catch (error) {
    console.error(error);
    alert('error');
  }
});

export async function processError(error: any) {
  await setLocalStorageData({
    [LocalStorageKey.ErrorMessage]: error?.message,
  });
}

// Proxy listeners

chrome.proxy.settings.onChange.addListener(
  handleProxyControlledByOtherExtensions,
);

if (chrome.storage.session.setAccessLevel) {
  void chrome.storage.session.setAccessLevel({
    accessLevel: 'TRUSTED_AND_UNTRUSTED_CONTEXTS',
  });
}

chrome.proxy.settings.get({}, handleProxyControlledByOtherExtensions);

initNetworkConnectionMonitor();
initIcon();
