import LZString from 'lz-string';
import { Alarms } from '../../../app/alarms/shared';
import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '../../../app/storage/local/shared';
import { setSessionProxyData } from '../../../app/storage/session/shared';
import { BaseProxyData } from './types/ProxyData';

enum PCAReplaceKey {
  Host = '${PROXY_HOST}',
  Port = '${PROXY_PORT}',
  VPNHost = '${PROXY_VPN_HOST}',
  VPNPort = '${PROXY_VPN_PORT}',
}

type GetPacScriptDto = Pick<BaseProxyData, 'host' | 'port'> & {
  pacString: string;
  shouldDecompress?: boolean;
  vpnHost?: string | null;
  vpnPort?: number | null;
};

const getPacScript = ({
  host,
  port,
  pacString,
  shouldDecompress,
  vpnHost,
  vpnPort,
}: GetPacScriptDto): string => {
  const decompressedScript = shouldDecompress
    ? LZString.decompressFromBase64(pacString)
    : pacString;

  let scriptWithReplacements = decompressedScript
    .replaceAll(PCAReplaceKey.Host, host)
    .replaceAll(PCAReplaceKey.Port, `${port}`);

  if (vpnHost && vpnPort) {
    scriptWithReplacements = scriptWithReplacements
      .replaceAll(PCAReplaceKey.VPNHost, vpnHost)
      .replaceAll(PCAReplaceKey.VPNPort, `${vpnPort}`);
  }

  return scriptWithReplacements;
};

export function setProxy({
  host,
  port,
  pacString,
  shouldDecompress,
  vpnHost,
  vpnPort,
}: GetPacScriptDto): void {
  chrome.proxy.settings.set(
    {
      value: {
        mode: 'pac_script',
        pacScript: {
          data: getPacScript({
            host,
            port,
            pacString,
            shouldDecompress,
            vpnHost,
            vpnPort,
          }),
        },
      },
      scope: 'regular',
    },
    async () => {
      if (chrome.runtime.lastError) {
        await setLocalStorageData({ currentState: 'error' });
        console.error('Error setting proxy:', chrome.runtime.lastError.message);
      } else {
        const { connectionCount, targetState } = await getLocalStorageData([
          LocalStorageKey.ConnectionCount,
          LocalStorageKey.TargetState,
        ]);
        const count = connectionCount ?? 0;
        if (targetState === 'disconnected') {
          void chrome.alarms.create(Alarms.DisableProxy, {
            delayInMinutes: 0,
          });
        } else {
          await setSessionProxyData(host, port);
          await setLocalStorageData({
            currentState: 'connected',
            connectionCount: count + 1,
          });
        }
      }
    },
  );
}

export function clearProxy(): void {
  chrome.proxy.settings.clear({ scope: 'regular' }, async () => {
    const { targetState } = await getLocalStorageData(
      LocalStorageKey.TargetState,
    );
    if (chrome.runtime.lastError) {
      await setLocalStorageData({ currentState: 'error' });
      console.error(
        'Error clearing proxy settings:',
        chrome.runtime.lastError.message,
      );
    } else if (targetState === 'connected') {
      await chrome.alarms.create(Alarms.EnableProxy, {
        delayInMinutes: 0,
      });
    } else {
      await setLocalStorageData({ currentState: 'disconnected' });
    }
  });
}
