import { SessionStorageKey } from '@app/storage/session/shared';
import { getSessionStorageData } from '@app/storage/session/shared';

export async function handleActiveTabWhenProxyControlledByOtherExtensions(
  activeInfo: chrome.tabs.TabActiveInfo,
): Promise<void> {
  const tab = await chrome.tabs.get(activeInfo.tabId);
  const window = await chrome.windows.getCurrent();

  if (window.id === tab.windowId) {
    const { isProxyControlledByOtherExtensions, popupWasOpened } =
      await getSessionStorageData([
        SessionStorageKey.IsProxyControlledByOtherExtensions,
        SessionStorageKey.PopupWasOpened,
      ]);

    if (isProxyControlledByOtherExtensions && !popupWasOpened) {
      await chrome.action.openPopup({ windowId: window.id });
    } else {
      chrome.tabs.onActivated.removeListener(
        handleActiveTabWhenProxyControlledByOtherExtensions,
      );
    }
  }
}
