import { ProxyCredential } from './types/ProxyData';

export function initUpdateProxyAuth(proxyCredential: ProxyCredential) {
  console.warn('Proxy auth init/update started');

  async function proxyAuthListener(
    details: chrome.webRequest.WebAuthenticationChallengeDetails,
    callback?: (response: chrome.webRequest.BlockingResponse) => void,
  ) {
    if (!callback) {
      return;
    }

    if (details.isProxy) {
      if (!proxyCredential) {
        callback({});
        return;
      }

      const { challenger } = details;
      const { host, port } = challenger;

      const credentials = proxyCredential[`${host}:${port}`];

      if (credentials.userName && credentials.password) {
        const { userName, password } = credentials;

        callback({
          authCredentials: {
            username: userName,
            password: password,
          },
        });
      } else {
        console.error(`No credentials found for proxy ${host}:${port}`);
        callback({});
      }
    } else {
      callback({});
    }
  }

  if (chrome.webRequest.onAuthRequired.hasListener(proxyAuthListener)) {
    chrome.webRequest.onAuthRequired.removeListener(proxyAuthListener);
  }

  chrome.webRequest.onAuthRequired.addListener(
    proxyAuthListener,
    { urls: ['<all_urls>'] },
    ['asyncBlocking'],
  );

  console.warn('Proxy auth init/update finished');
}
