// Google Analytics Measurement Protocol implementation
const GA_ENDPOINT = 'https://www.google-analytics.com/mp/collect';
const MEASUREMENT_ID = process.env.GA_MEASUREMENT_ID;
const API_SECRET = process.env.GA_API_SECRET;
const SESSION_EXPIRATION_IN_MIN = 30;
const DEFAULT_ENGAGEMENT_TIME_IN_MSEC = 100;

interface GAEvent {
  name: string;
  params?: Record<string, any>;
}

class GoogleAnalytics {
  private async getOrCreateClientId(): Promise<string> {
    const result = await chrome.storage.local.get('clientId');
    let clientId = result.clientId;
    if (!clientId) {
      clientId = self.crypto.randomUUID();
      await chrome.storage.local.set({ clientId });
    }
    return clientId;
  }

  private async getOrCreateSessionId(): Promise<string> {
    // Store session in memory storage
    let { sessionData } = await chrome.storage.session.get('sessionData');
    // Check if session exists and is still valid
    const currentTimeInMs = Date.now();
    if (sessionData && sessionData.timestamp) {
      // Calculate how long ago the session was last updated
      const durationInMin = (currentTimeInMs - sessionData.timestamp) / 60000;
      // Check if last update lays past the session expiration threshold
      if (durationInMin > SESSION_EXPIRATION_IN_MIN) {
        // Delete old session id to start a new session
        sessionData = null;
      } else {
        // Update timestamp to keep session alive
        sessionData.timestamp = currentTimeInMs;
        await chrome.storage.session.set({ sessionData });
      }
    }
    if (!sessionData) {
      // Create and store a new session
      sessionData = {
        session_id: currentTimeInMs.toString(),
        timestamp: currentTimeInMs.toString(),
      };
      await chrome.storage.session.set({ sessionData });
    }
    return sessionData.session_id;
  }

  async sendEvent(event: GAEvent): Promise<void> {
    try {
      if (!MEASUREMENT_ID) {
        console.warn('GA_MEASUREMENT_ID is not set in .env file');
        return;
      }

      if (!API_SECRET) {
        console.warn('GA_API_SECRET is not set in .env file');
        return;
      }

      const [clientId, sessionId] = await Promise.all([
        this.getOrCreateClientId(),
        this.getOrCreateSessionId(),
      ]);

      const payload = {
        client_id: clientId,
        events: [
          {
            name: event.name,
            params: {
              ...event.params,
              session_id: sessionId,
              engagement_time_msec: DEFAULT_ENGAGEMENT_TIME_IN_MSEC,
            },
          },
        ],
      };

      const response = await fetch(
        `${GA_ENDPOINT}?measurement_id=${MEASUREMENT_ID}&api_secret=${API_SECRET}`,
        {
          method: 'POST',
          body: JSON.stringify(payload),
        },
      );

      if (!response.ok) {
        console.error('Failed to send GA event:', await response.text());
      }
    } catch (error) {
      console.error('Error sending GA event:', error);
    }
  }

  // Helper methods for common events
  async trackPageView(page: string): Promise<void> {
    await this.sendEvent({
      name: 'page_view',
      params: {
        page_title: page,
        page_location: chrome.runtime.getURL(page),
      },
    });
  }

  async trackUserAction(
    action: string,
    category: string,
    label?: string,
  ): Promise<void> {
    await this.sendEvent({
      name: 'user_action',
      params: {
        action,
        category,
        label,
      },
    });
  }

  async trackError(error: string, errorType: string): Promise<void> {
    await this.sendEvent({
      name: 'extension_error', // Note: 'error' is a reserved event name
      params: {
        error_message: error,
        error_type: errorType,
      },
    });
  }
}

export const analytics = new GoogleAnalytics();
