import {
  getLocalStorageData,
  LocalStorageKey,
  type PACRegistryData,
} from '../app/storage/local/shared';
import { configsClient } from './configsClient';
import { httpClient } from './httpClient';

export async function fetchPACData<T>(registryUrl: string): Promise<T> {
  if (!registryUrl) {
    throw new Error('registryUrl is required');
  }

  const data = await httpClient.get<T>(registryUrl).json();

  return data;
}

export async function fetchPACRegistry(): Promise<PACRegistryData> {
  const { configsBaseUrl } = await getLocalStorageData(
    LocalStorageKey.ConfigsBaseUrl,
  );

  const pacRegistryFullUrl =
    configsBaseUrl === process.env.CONFIG_BASE_URL_2
      ? new URL(process.env.PAC_REGISTRY_URL_YANDEX, configsBaseUrl)
      : new URL(
          process.env.PAC_REGISTRY_URL_GOOGLE,
          process.env.CONFIG_BASE_URL_1,
        );

  const data = await configsClient
    .get<PACRegistryData>(pacRegistryFullUrl.toString())
    .json();
  return data;
}
